$collection-page-cta-height: 32px;
$collection-page-cardcolor-shadow-color: rgb(46,46,80);

.p__collection-page {
    .main-content {
        padding-top: 0;

        h1 {
            margin-top: rem(2pc);
        }
    }

    .collection-page {
        &__data {
            padding-top: rem(24px);
            border-top: 1px solid rgba(100,100,100,.1);
        }

        &__child {
            height: 100%;
            display: flex;
            flex-direction: column;
            padding: 0;
            box-shadow: none;
            border: 0 solid transparent;
            position: relative;
            @include border-radius(5px, 5px, 0, 0);
            @include box-shadow(0, 0, 1px, 0, rgba($collection-page-cardcolor-shadow-color, .9));

            &:hover {
                @include box-shadow(0, 0, 8px,0, rgba($collection-page-cardcolor-shadow-color, .4));
                @include scale(102%, 102%);
            }

            &-container {
                margin-bottom: rem(24px);
            }

            &.primaryColor {
                background-color: $color-white;
            }

            &.secondaryColor {
                background-color: $color-secondary;
            }

            &.thirdColor {
                background-color: $color-primary;
            }

            &-text {
                padding: rem(12px 8px);
                font-size: rem(12px);

                h3 {
                    font-size: rem(16px);
                    font-weight: 600;
                }
            }

            figure {
                width: calc(100% + 2px);
                @include border-radius(5px, 5px, 0, 0);
                overflow: hidden;

                img {
                    height: rem(200px);
                    position: relative;
                    left: rem(-1px);
                    width: calc(100% + 2px);
                    object-fit: cover;
                    object-position: center;
                }

                background: rgba(200,200,200,.1);
            }

            &-cta {
                @include transition;
                background: rgba(255,255,255,.9);
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                height: rem($collection-page-cta-height);
                font-size: rem(14px);
                font-weight: 600;
                @include box-shadow(0, 0, 1px, 0, rgba($collection-page-cardcolor-shadow-color, .6));
                margin: auto 0 0 0;

                &:hover {
                    background: rgba($color-black,.9);
                    color: $color-white;
                }
            }
        }

        &__title {
            font-size: $fontsize-h2;
            color: $color-primary;

            &.white {
                color: $color-white;
            }
        }

        &__subtitle {
            font-size: $fontsize-normal;
            text-transform: uppercase;
        }

        &__text {
            max-height: 75%;
            overflow: hidden;

            &, p {
                text-align: justify;
            }

            @media (max-width: 700px) {
                max-height: none;
            }

            li {
                list-style-type: disc;
                margin-left: 1.2rem;
            }

            &.white {
                color: $color-white;
            }
        }

        &__button {
            display: flex;
            padding-top: 1rem;

            .primaryButton {
                min-width: rem(212px);
                width: auto;
                height: rem(44px);
                border-radius: rem(50px);
                border: 2px solid $color-primary;
                color: $color-primary;
                padding: 0.5rem 1rem;
                display: flex;
                justify-content: center;
                align-items: center;
            }

            .secondaryButton {
                min-width: rem(212px);
                width: auto;
                height: rem(44px);
                border-radius: rem(50px);
                border: 2px solid $color-secondary;
                padding: 0.5rem 1rem;
                color: $color-primary;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: $color-secondary;
            }

            .thirdButton {
                min-width: rem(212px);
                width: auto;
                height: rem(44px);
                border-radius: rem(50px);
                border: 2px solid $color-secondary;
                color: $color-secondary;
                padding: 0.5rem 1rem;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
    }
}