.usp-block {
    width: 100%;
    justify-content: center;
    margin: 3rem 0;
    padding: 2rem;
    background-color:lightgray;
    &.primaryColor {
        background-color: $color-white;
    }

    &.secondaryColor {
        /*background-color: $color-grey-5;*/
    }

    &.thirdColor {
        /*background-color: $color-primary-25;*/
    }

    &__title-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 0 1rem 2rem 0;

        &__title {
            font-weight: $font-weight-normal;
            /*font-size: $fontsize-x-large;*/
            color: $color-primary;
            text-align: center;
        }

        &__subtitle {
            /*font-weight: $font-weight-black;*/
            /*font-size: $fontsize-base;*/
            color: $color-primary;
            padding-bottom: 1rem;
            text-align: center;
        }

        &__text {
            /*font-size: $fontsize-base;*/
            font-weight: $font-weight-bold;
            max-width: rem(630px);
            text-align: center;
            padding-bottom: 2.5rem;
        }
    }

    &__items {
        display: flex;
        justify-content: center;


        @media (max-width: $tablet-width) {
            display: grid;
            grid-template-columns: auto auto;
        }

        @media (max-width: 680px) {
            display: grid;
            grid-template-columns: auto;
        }

        &__item {
            display: grid;
            max-width: rem(380px);
            min-height: auto;
            padding: 0.5rem;
            min-width: 25%;

            &.link-only-layout {
                display: flex;
                flex-direction: row-reverse;
                justify-content: space-between;
                align-items: center;
                font-size: $fontsize-h3;
            }

            &__title-wrapper {
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: rem(10px);
                flex-direction: column;
                background-color: gray;
                padding: 2rem 0 1rem 0;

                &__title {
                    font-size: $fontsize-h3;
                }

                &__image-holder {
                    width: rem(65px);
                    height: rem(65px);
                    margin-top: -4rem;

                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
            }

            &__text {
                padding: 1rem;
            }
        }

        &__button-holder {
            .primaryCTA {
                color: $color-white;
                /*font-weight: $font-weight-black;*/
                display: flex;
                align-items: center;
                gap: rem(5px);
                text-transform: uppercase;

                &:after {
                    content: '';
                    display: block;
                    width: 3rem;
                    height: 2rem;
                    /*@include icon--arrow-blue();*/
                }
            }

            .secondaryCTA {
                color: $color-white;
                /*       font-weight: $font-weight-black;*/
                display: flex;
                align-items: center;
                gap: rem(10px);
                text-transform: uppercase;

                &:after {
                    content: '';
                    display: block;
                    width: 1rem;
                    height: 1rem;
                    /*@include icon--arrow-blue-tiny();*/
                }
            }

            .thirdCTA {
                color: $color-white;
                /*font-weight: $font-weight-black;*/
                display: flex;
                align-items: center;
                justify-content: center;
                gap: rem(10px);
                text-transform: uppercase;
                border-radius: rem(50px);
                border: 2px solid $color-white;
                padding: 0.5rem 2rem;
            }
        }
    }
}
