.text-block {
    display: flex;
    flex-direction: row;


    @media (max-width: 700px) {
        flex-direction: column;
    }

    &__item {
        display: flex;
        flex-direction: column;
        padding: 1rem;


        @media (max-width: 700px) {
            display: grid;
            max-height: none;
        }

        &.primaryColor {
            background-color: $color-white;
        }

        &.secondaryColor {
            background-color: $color-secondary;
        }

        &.thirdColor {
            background-color: $color-primary;
        }
    }



    &__title {
        font-size: $fontsize-h2;
        color: $color-primary;

        &.white {
            color: $color-white;
        }
    }

    &__subtitle {
        font-size: $fontsize-normal;
        text-transform: uppercase;
    }

    &__text {
        max-height: 75%;
        overflow: hidden;

        &, p {
            text-align: justify;
        }

        @media (max-width: 700px) {
            max-height: none;
        }

        li {
            list-style-type: disc;
            margin-left: 1.2rem;
        }



        &.white {
            color: $color-white;
        }
    }

    &__button {
        display: flex;
        padding-top: 1rem;

        .primaryButton {
            min-width: rem(212px);
            width: auto;
            height: rem(44px);
            border-radius: rem(50px);
            border: 2px solid $color-primary;
            color: $color-primary;
            padding: 0.5rem 1rem;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .secondaryButton {
            min-width: rem(212px);
            width: auto;
            height: rem(44px);
            border-radius: rem(50px);
            border: 2px solid $color-secondary;
            padding: 0.5rem 1rem;
            color: $color-primary;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: $color-secondary;
        }

        .thirdButton {
            min-width: rem(212px);
            width: auto;
            height: rem(44px);
            border-radius: rem(50px);
            border: 2px solid $color-secondary;
            color: $color-secondary;
            padding: 0.5rem 1rem;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
}
