$form-active-color: #b6ff00;

.form {
    &__input, &__select {
        padding-right: rem(32px);
        background-size: rem(12px 12px);

        &:hover {
            border-color: $form-active-color;
        }

        option {
            &:hover {
                box-shadow: 0 0 10px 100px $form-active-color inset; // @include box-shadow(0, 0, 10px, 10px, $paradox-color-yellow, inset);
                background: $form-active-color !important;
            }
        }
    }
}
