.hero-block {
    .overlay {
        position: absolute;
        width: 100%;
        height: rem(450px);
        display: block;
        background: linear-gradient(to left, transparent 0%, black 150%);

        @media (max-width: 640px) {
            height: 100%;
        }
    }

    .extra-padding {
        padding-left: rem(180px);
        padding-top: 4rem;

        @media (max-width: 640px) {
            padding-left: rem(115px);
        }
    }

    .narrow {
        height: rem(650px);

        .overlay {
            width: -webkit-fill-available;
            height: rem(570px);
            margin-right: 2.5rem;
        }
    }

    &__content {
        display: flex;
        height: rem(450px);
        margin: 1rem 0;
        align-items: center;

        @media (max-width: 640px) {
            height: auto;
            min-height: rem(480px);
        }
    }

    &__text-wrapper {
        position: relative;
        z-index: 2;
        padding-left: rem(110px);

        @media (max-width: 640px) {
            padding-left: rem(50px);
        }

        &__title {
            /*font-weight: $font-weight-extra-black;*/
            /*font-size: $fontsize-large-heading;*/
            color: $color-white;
            letter-spacing: 0.06em;
            text-transform: uppercase;
            line-height: 1;

            @media (max-width: 640px) {
                font-size: $fontsize-small;
            }
        }

        &__title-yellow {
            /*font-weight: $font-weight-extra-black;*/
            /*font-size: $fontsize-large-heading;*/
            /*color: $color-accent;*/
            letter-spacing: 0.06em;
            text-transform: uppercase;
            line-height: 1;

            @media (max-width: 640px) {
                font-size: $fontsize-small;
            }
        }

        &__subtitle {
            font-size: $fontsize-h2;
            /*font-weight: $font-weight-bold;*/
            color: $color-white;
            line-height: rem(38px);
        }

        &__text {
            /*font-size: $fontsize-base;*/
            /*font-weight: $font-weight-bold;*/
            line-height: rem(24px);
            color: $color-white;
            max-width: rem(600px);

            @media (max-width: 900px) {
                max-width: 70%;
            }

            &.quote-style {
                margin-left: auto;
                display: flex;
                justify-content: flex-end;
                max-width: 50%;
                font-style: italic;
                font-size: $fontsize-small;
                /*font-weight: $font-weight-bold;*/
                line-height: rem(30px);
                padding-top: 15%;
                padding-right: 10%;

                &:before {
                    content: '';
                    display: block;
                    width: rem(50px);
                    height: rem(70px);
                    padding: 2rem;
                    margin-top: -2rem;
                    /*@include icon--quote-white();*/

                    @media (max-width: 640px) {
                        display: none;
                    }
                }

                @media (max-width: 850px) {
                    margin-left: 1rem;
                    padding-right: 0;
                    max-width: 60%;
                }
            }
        }

        &__button {
            max-width: rem(300px);
            padding-top: 1rem;

            .primaryCTA {
                background: transparent;
                /*font-weight: $font-weight-extra-bold;*/
                color: $color-white;
                display: grid;
                grid-template-columns: auto auto;
                align-items: center;

                &:after {
                    content: '';
                    display: block;
                    width: auto;
                    height: 1rem;
                    /*@include icon--yellow-arrow();*/
                }
            }

            .secondaryCTA {
                color: $color-white;
                border-radius: rem(50px);
                padding: 0.7rem 2rem;
            }

            .thirdCTA {
                background: transparent;
                border: 2px solid $color-white;
                color: $color-white;
                border-radius: rem(50px);
                padding: 0.7rem 2rem;
            }
        }
    }

    &__image-wrapper {
        width: 100%;
        height: rem(450px);
        position: absolute;

        img {
            max-height: rem(450px);
            width: 100%;
            object-fit: cover;
            height: 100%;
        }

        &.narrow {
            padding: rem(40px);

            img {
                max-height: rem(650px);
            }

            .overlay {
                height: rem(570px);
                margin-right: 2.5rem;
                width: -webkit-fill-available;
            }
        }
    }

    &__image-wrapper-mobile {
        width: 100%;
        height: rem(480px);
        position: absolute;

        img {
            width: 100%;
            object-fit: cover;
            height: 100%;
        }

        &.narrow {
            padding: rem(40px);
        }
    }

    &__haki-image {
        position: absolute;
        max-height: rem(200px);
        max-width: rem(150px);

        &.right {
            right: 0;
        }

        @media (max-width: 640px) {
            max-height: rem(110px);
            max-width: rem(65px);
        }
    }
}
